.question {
  position: relative;
  padding-top: 92px;
  padding-bottom: 86px;
  padding-left: 10%;
  padding-right: 6%;

  background-color: #00b2ff;
  background-image: url("../img/triangle-form.png");
  background-repeat: no-repeat;

  background-position-x: 101%;
  background-position-y: 35px;

  @media (max-width: $tablet-width) {
    padding: 0;
    padding-top: 80px;
    padding-bottom: 55px;

    background-image: none;
  }

  @media (max-width: $mobile-width) {
    padding-top: 27px;
    padding-bottom: 16px;
  }
}

.question__wrapper {
  display: flex;
  flex-direction: row;

  @media (max-width: $tablet-width) {
    flex-direction: column;
  }
}

.question__image {
  margin-right: 5.6%;
  padding-top: 71px;
  padding-left: 10%;

  background-image: url("../img/triangle1.png");
  background-repeat: no-repeat;
  background-position-x: -15px;
  background-position-y: -151px;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.question__image img {
  // max-width: 511px;
  margin-right: 15%;
}

.question__form {
  padding-bottom: 25px;
  padding-left: 18px;

  @media (max-width: $tablet-width) {
    padding-left: 0;
    align-self: center;
  }

  @media (max-width: $mobile-width) {
    padding-left: 5%;
    padding-right: 5%;

    width: 100%;
  }
}

.question__form form {
  display: flex;
  padding-left: 4px;
  flex-wrap: wrap;
  justify-content: space-between;

  width: 573px;

  @media (max-width: $tablet-width) {
    width: 595px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
    flex-direction: column;

    width: 100%;
  }
}

.question__form p {
  margin: 0;
  margin-bottom: 12px;
  padding: 0;

  font-family: "RobotoCondesed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;

  color: $white;

  @media (max-width: $tablet-width) {
    margin-bottom: 19px;

    font-size: 32px;
    line-height: 140.62%;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 14px;

    font-size: 28px;
    width: 100%;
  }
}

.question__form span {
  display: block;
  margin: 0;
  margin-bottom: 55px;
  padding: 0;
  padding-left: 5px;

  color: $white;

  @media (max-width: $mobile-width) {
    margin-bottom: 27px;
    padding-left: 0;

    font-size: 16px;
    line-height: 22px;
  }
}


.question__form textarea::placeholder,
.question__form input::placeholder {

  font-family: "PTmono", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;

  font-size: 16px;
  line-height: 22px;

  color: $white;
}

.question__form input {
  margin-bottom: 40px;
  padding-top: 10px;
  padding-bottom: 15px;
  padding-left: 18px;
  padding-right: 10px;

  width: 270px;

  @media (max-width: $tablet-width) {
    margin-bottom: 42px;
    padding-top: 14px;

    width: 280px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 32px;

    padding-top: 11px;
    padding-bottom: 17px;
    padding-left: 16px;

    width: 100%;
  }
}

.question__form textarea,
.question__form input {

  font-size: 16px;
  line-height: 22px;

  color: $white;

  background-color: rgba(255, 255, 255, 0.1);

  border: none;

  opacity: 1;
  transition: all 0.3s ease;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  @media (max-width: $tablet-width) {
    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}

.question__form input[type="tel"] {
  width: 269px;

  @media (max-width: $tablet-width) {
    width: 279px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.question__form textarea {
  margin-bottom: 43px;
  padding-top: 12px;
  padding-left: 18px;
  padding-right: 10px;

  width: 570px;
  height: 154px;

  resize: none;

  @media (max-width: $tablet-width) {
    margin-bottom: 46px;

    width: 592px;
    height: 160px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 29px;

    width: 100%;
    height: 104px;
  }
}

.question__form button {
  min-width: 271px;

  @media (max-width: $tablet-width) {
    min-width: 280px;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 23px;
  }
}

.question__form--agreement {
  position: relative;
  padding-top: 5px;
  padding-left: 40px;
  margin-right: 27px;
  width: 240px;

  order: 1;

  @media (max-width: $tablet-width) {
    width: 249px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 0;
    padding-left: 33px;

    width: 100%;
  }
}

.question__form--agreement label {
  font-size: 14px;
  line-height: 16px;

  color: $white;

  opacity: 1;
  transition: all 0.3s ease;

  &::before {
    content: "";
    position: absolute;

    width: 16px;
    height: 16px;

    left: 8px;
    top: 9px;

    border: 1px solid #ffffff;

    box-sizing: border-box;

    @media (max-width: $mobile-width) {
      left: 0;
    }
  }

  &:hover,
  &:focus {
    opacity: 0.7;
    cursor: pointer;

    @media (max-width: $tablet-width) {
      opacity: 1;
    }
  }
}

.question__form--agreement input:focus + label {
  opacity: 0.7;

  @media (max-width: $tablet-width) {
    opacity: 1;
  }
}

.question__form--agreement input:checked + label::before {
  background-image: url("../img/checed.png");
  background-repeat: no-repeat;
  background-position: center;
}

